<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {queryRegisterByTel, addRegister,querySubscriptionList,formatDate} from '@/api'
export default {
  components: {
    ViewHeader
  },
  filters: {
        formatDate(time) {
            var date = new Date(time);
            return formatDate(date, 'yyyy-MM-dd');
        },
        formatDate2(time) {
            var date = new Date(time);
            return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
        }
    },
  data () {
    return {
      telPhone: '',
      name: '',
      idCard: '',
      input: '',
      sex: '',
      age: '',
      nowDate: '',
      userId: '',
      subId: '',
      createTime: '',
      clinicTime: '',
      value1: '',
      value2: '',
      dialogVisible: false,
      dialogVisible2: false,
      dialogVisible3: false,
      dialogVisible4: false,
      dialogVisible5: false,
      dialogVisible6: false,
      dialogVisible7: false,
      activeIndex: 0,
      status: 0,
      pageSizes: [100, 200, 300, 400],
      pageSize: 100,
      total: 10000,
      pageNo: 1,
      list: null
    }
  },
  methods: {
    _search () {
      var _this = this
      var params = new URLSearchParams()
      params.append('telPhone', this.telPhone)
      queryRegisterByTel(params).then(function (data) {
        _this.status = data.data.isRegister
        if (_this.status === '1') {
          _this.dialogVisible5 = true
        }
        if (_this.status === '2') {
          _this.dialogVisible6 = true
          _this.name = data.data.user.realName
          _this.idCard = data.data.user.idCard
          _this.sex = data.data.user.sex
          _this.age = data.data.age
          _this.nowDate = data.data.nowDate
          _this.userId = data.data.user.userId
        }
        if (_this.status === '3') {
          _this.dialogVisible7 = true
          _this.name = data.data.subscription.patientName
          _this.createTime = data.data.createTime
          _this.clinicTime = data.data.clinicTime
          _this.subId = data.data.subscription.subscriptionId
        }
        if (_this.status === '4') {
          _this.dialogVisible7 = true
          _this.name = data.data.subscription.patientName
          _this.createTime = data.data.createTime
          _this.clinicTime = data.data.clinicTime
          _this.subId = data.data.subscription.subscriptionId
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
    },
    _register () {
      var params = new URLSearchParams()
      params.append('telPhone', this.telPhone)
      params.append('name', this.name)
      params.append('age', this.age)
      params.append('sex', this.sex)
      params.append('idCard', this.idCard)
      params.append('status', this.status)
      params.append('userId', 0)
      params.append('subId', 0)
      addRegister(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          alert('挂号成功')
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
      this.dialogVisible5 = false
      Object.assign(this.$data, this.$options.data())
    },
    _registerAdd () {
      var params = new URLSearchParams()
      params.append('telPhone', this.telPhone)
      params.append('name', this.name)
      params.append('age', this.age)
      params.append('sex', this.sex)
      params.append('idCard', this.idCard)
      params.append('status', this.status)
      params.append('userId', this.userId)
      params.append('subId', 0)
      addRegister(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          alert('挂号成功')
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
      this.dialogVisible6 = false
      Object.assign(this.$data, this.$options.data())
    },
    _registerUpdate () {
      var params = new URLSearchParams()
      params.append('telPhone', '')
      params.append('name', '')
      params.append('idCard', '')
      params.append('status', this.status)
      params.append('userId', 0)
      params.append('subId', this.subId)
      addRegister(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          alert('挂号成功')
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
      this.dialogVisible7 = false
      Object.assign(this.$data, this.$options.data())
    },
    _diaclose () {
      Object.assign(this.$data, this.$options.data())
    },
    _querySubList (val) {
      var _this2 = this
      var params = new URLSearchParams()
      params.append('telPhone', '15380320110')
      params.append('startDate', this.value1)
      params.append('endDate', '')
      params.append('clincSartDate', this.value2)
      params.append('clincEndDate', '')
      params.append('currentPage',this.pageNo)
      params.append('pageSize', this.pageSize)
      params.append('status', '')
      querySubscriptionList(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
        _this2.list = data.data
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
    }
  }
}
