  height: 40px
  font-size: $size-16
.tablebtn
  font-size: $size-14
  color: $color-back-green_0
  font-weight: bold
.dialogBtn
  border: 1px solid $color-back-blue_1
  padding: 15px 30px
  font-size: $size-16
  color: $color-back-blue_1
  font-weight: bold
  background-color: rgba(0, 0, 0, 0)
.tab-item
  @extend .pointer
  color: $color-back-black_1
  font-size: $size-14
  padding: 10px 15px
  background-color: $color-back-gray_4
  margin: 0 10px
.actived
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bold
.guahaodialog
  .item
    width: 48%
    margin-top: 10px
    p
      color: $color-back-black_1
      font-size: $size-14
      .dot
        color: $color-back-blue_2
.red
  color: $color-back-red_0
</style>


